export default {
  data() {
    return {
      form: {
        id: null,
        studentName: "",
        password: "",
        className: "",
        role: 3,
        status: 1,
        age: "",
        sex: "",
        birthDay: null,
        phone: null
      },
      formLoading: false,
      rules: {
        studentName: [{
          required: true,
          message: "请输入用户名",
          trigger: "blur"
        }],
        className: [{
          required: true,
          message: "请输入真实姓名",
          trigger: "blur"
        }]
      }
    };
  },
  created() {
    let id = this.$route.query.id;
    let _this = this;
    if (id && parseInt(id) !== 0) {
      _this.formLoading = true;
      console.log(id);
    }
  },
  methods: {
    resetForm() {
      let lastId = this.form.id;
      this.$refs["form"].resetFields();
      this.form = {
        id: null,
        studentName: "",
        password: "",
        className: "",
        role: 3,
        status: 1,
        age: "",
        sex: "",
        birthDay: null,
        phone: null
      };
      this.form.id = lastId;
    }
  }
};